    }
    .c_grid--double > *:nth-child(4n-1) {
      flex-basis: 50%;
    }
  }
}
@media screen and (min-width: 44rem) {
  .c_grid--single {
    padding: 0;
    margin: 0 auto;
    max-width: 44rem;
  }
}
